/**
 * 
 * 
 * @export
 * @param {array} 
 * @param {int} x  
 * @param {int} y 
 * @param {string} dir  //up down left right
 * @return {any}  // false  0 1
 */
export function detectCollision(status, x, y, dir) {
    // 
    const dir2num = {
        'up': 0,
        'down': 2,
        'right': 1,
        'left': 3
    };
    // 
    let nextBlock = -1;
    switch (dir2num[dir]) {
        case 0:
            nextBlock = status[x - 1][y];
            break;
        case 1:
            nextBlock = status[x][y + 1];
            break;
        case 2:
            nextBlock = status[x + 1][y];
            break;
        case 3:
            nextBlock = status[x][y - 1];
            break;
        default:
            nextBlock = -1;
            break;
    }
    return nextBlock === 2 ? false : nextBlock;
}


